<template>
<div class="container">
   <div class="py-5 text-center">
 <h2>로그인</h2>
 </div>

  <div class="form-group">
    <label for="exampleId">로그인 ID</label>
    <input type="text" class="form-control" id="exampleId" aria-describedby="emailHelp" placeholder="Enter Id" v-model="loginId">
  </div>
  <div class="form-group mt-4">
    <label for="exampleInputPassword1">Password</label>
    <input type="password" class="form-control" id="exampleInputPassword1" placeholder="Password" v-model="password">
  </div>

  <hr class="my-4">

  <div class="row">
 <div class="col">
 <button class="w-100 btn btn-primary btn-lg" type="button" @click="login()">로그인</button>
 </div>
 <div class="col">
 <button class="w-100 btn btn-secondary btn-lg" type="button">취소</button>
 </div>
 </div>
</div>
</template>

<script>


import axios from "axios";

export default {
  name:'login-view',
  data(){
    return{
      loginId:'',
      password:'',
    }
  },
  methods:{
    login(){
      axios.post('/api/members/login',
          {
            loginId:this.loginId,
            password:this.password,
          },
          {withCreadentials:true}).then(()=>{
        this.$router.replace('/');
      });
    },
  },
}
</script>

<style>
.container {
  max-width: 560px;
}
</style>