import { createWebHistory, createRouter } from "vue-router";
import Register from "@/components/Register";
import Home from "@/components/Home";
import Read from "@/components/Read";
import Edit from "@/components/Edit";
import Signup from "@/components/signup";
import login from "@/components/login";
import axios from "axios";
/* eslint-disable no-useless-escape */

const routes = [
    {
        path: "/register",
        component: Register,
        beforeEnter:(to,from,next)=>{
            axios.get('/api/members/validate').then((res)=>{
                console.log(res.status);
                next();
            }).catch((error)=>{
                console.log(error);
                alert('상품 등록시 로그인을 먼저해야합니다.')
                next(false);
            })
        }
    },
    {
        path: "/home",
        component: Home
    },
    {
        path: "/read/:productId",
        component: Read
    },
    {
        path: "/edit/:productId",
        component: Edit,
        beforeEnter:(to,from,next)=>{
            axios.get('/api/members/validate').then((res)=>{
                console.log(res.status);
                next();
            }).catch((error)=>{
                console.log(error);
                alert('상품 수정시 로그인을 먼저해야합니다.')
                next(false);
            })
        }
    },
    {
        path: "/",
        component: Home
    },
    {
        path:"/signup",
        component: Signup
    },
    {
        path:"/login",
        component: login,
        beforeEnter:(to,from,next)=>{
            axios.get('/api/members/validate').then((res)=>{
                console.log(res.status);
                alert('이미 로그인 되어있습니다.')
                next(false);
            }).catch((error)=>{
                console.log(error);
                next();
            })
        }
    }
];

const router = createRouter({
    history: createWebHistory(),
    routes,
});


export default router;