<template>
   <div class="mt-2">
     <p>상품 제목</p>
     <el-input v-model="data.title" placeholder="상품 제목" />
   </div>
   <div class="mt-3">
    <p>상품 가격</p>
    <el-input v-model.number="data.price" placeholder="상품 가격" />
   </div>
   <div class="mt-3">
     <p>상품 내용</p>
     <el-input
         v-model="data.content"
         :rows="10"
         type="textarea"
         placeholder="상품 내용"
     />
   </div>
  <div class="mb-3">
    <label for="formFile" class="form-label">사진을 등록하세요</label>
    <input class="form-control" type="file" id="formFile" accept=".png ,.jpg" @change="uploadImage">
  </div>
  <div class="uploadResult">
    <ul>

    </ul>
    <el-button type="warning" v-if="imgUpload==true" @click="deleteItem">사진 삭제</el-button>
  </div>
   <el-button type="warning"  style="float: right;" @click="edit">수정</el-button>
</template>


<script>
import axios from "axios";

export default {
  name:'Edit-View',
  data(){
    return{
      data:{},
      NowPage:this.$route.query.page,
      NowSize:this.$route.query.size,
      imgUpload:false,
    }
  },
  mounted(){
    axios.get(`/api/products/${this.$route.params.productId}`).then((response)=>{
      console.log(response.data);
      this.data = response.data;
    })
  },
  methods:{
    edit(){
      axios.patch(`/api/products/${this.$route.params.productId}`,{
        title:this.data.title,
        price:this.data.price,
        content:this.data.content,
        imgName: this.data.imgName,
        imguuid : this.data.uuid,
        imgpath: this.data.path,
      }).then(()=>{
        this.$router.replace(`/home?page=${this.NowPage}&size=${this.NowSize}`);
      })
    },
    uploadImage(e){
      var formData = new FormData();
      var imageFile = e.target.files[0];
      if(imageFile.type.indexOf('image')<0){
           alert('이미지만 올릴 수 있습니다.');
        document.querySelector('#formFile').value='';
      }else{
        formData.append("uploadFiles",imageFile);
        console.log(imageFile);

        axios.post('/api/uploadAjax',formData,{
          headers:{
            'Content-Type':'multipart/form-data'
          }
        }).then((response)=>{
          this.data.uuid = response.data[0].uuid;
          this.data.path = response.data[0].folderPath;
          this.data.imgName = response.data[0].fileName;
          var imgsrc = '/api/display?fileName='+response.data[0].imageURL;
          var image = new Image(150,150);
          image.src =imgsrc;
          document.querySelector('.uploadResult ul').innerHTML='';
          document.querySelector('.uploadResult ul').appendChild(image);
          this.imgUpload = true;

        })
      }

    },
    deleteItem(){
      document.querySelector('.uploadResult ul').innerHTML='';
      document.querySelector('#formFile').value='';
      this.uuid = '';
      this.path = '';
      this.imgName = '';
      this.imgUpload = false;

    },
  }
}


</script>


