<template>
   <div class="mt-2">
     <p>상품 제목</p>
     <el-input v-model="title" placeholder="상품 제목" />
   </div>
  <div class="mt-3">
    <p>상품 가격</p>
    <el-input v-model.number="price" placeholder="상품 가격" />
  </div>
   <div class="mt-3">
     <p>상품 내용</p>
     <el-input
         v-model="content"
         :rows="10"
         type="textarea"
         placeholder="상품 내용"
     />
   </div>
  <div class="mb-3">
    <label for="formFile" class="form-label">사진을 등록하세요</label>
    <input class="form-control" type="file" id="formFile" accept=".png ,.jpg" @change="uploadImage">
  </div>

  <div class="uploadResult">
    <ul>

    </ul>
    <el-button type="warning" v-if="imgUpload==true" @click="deleteItem">사진 삭제</el-button>
  </div>

   <el-button type="primary"  style="float: right;" @click="register">상품 등록</el-button>
</template>


<script>
import axios from 'axios';

export default{
  name:'Register-View',
  data(){
    return{
      title:'',
      content:'',
      price:0,
      imgName:'',
      uuid:'',
      path:'',
      imgUpload:false,

    }
  },
  methods:{
    register() {
      axios.post('/api/products', {
        title: this.title,
        price: this.price,
        content: this.content,
        imgName: this.imgName,
        imguuid : this.uuid,
        imgpath: this.path,
      })
          .then(() => {
            this.$router.replace('/home?page=1&size=9');
          });
    },

    uploadImage(e){
      var formData = new FormData();
      var imageFile = e.target.files[0];
      if(imageFile.type.indexOf('image')<0){
        alert('이미지만 올릴 수 있습니다.');
        document.querySelector('#formFile').value='';
      }else {
        formData.append("uploadFiles", imageFile);
        console.log(imageFile);

        axios.post('api/uploadAjax', formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }).then((response) => {
          this.uuid = response.data[0].uuid;
          this.path = response.data[0].folderPath;
          this.imgName = response.data[0].fileName;
          var imgsrc = 'api/display?fileName=' + response.data[0].imageURL;
          var image = new Image(150, 150);
          image.src = imgsrc;
          document.querySelector('.uploadResult ul').innerHTML = '';
          document.querySelector('.uploadResult ul').appendChild(image);
          this.imgUpload = true;

        })
      }
    },
    deleteItem(){
      document.querySelector('.uploadResult ul').innerHTML='';
      document.querySelector('#formFile').value='';
      this.uuid = '';
      this.path = '';
      this.imgName = '';
      this.imgUpload = false;

    },

  },


}

</script>

