<template>
<div class="container">
 <div class="py-5 text-center">
 <h2>회원 가입</h2>
 </div>

  <h4 class="mb-3">회원 정보 입력</h4>
  <div class="form-group">
    <label for="exampleId">로그인 ID</label>
    <input type="text" class="form-control" id="exampleId" aria-describedby="emailHelp" placeholder="Enter Id" v-model="loginId">
  </div>
  <div class="form-group mt-4">
    <label for="exampleInputPassword1">Password</label>
    <input type="password" class="form-control" id="exampleInputPassword1" placeholder="Password" v-model="password">
  </div>
  <div class="form-group mt-4">
    <label for="exampleName">이름</label>
    <input type="text" class="form-control" id="exampleName" aria-describedby="emailHelp" placeholder="Enter Name" v-model="nickname">
  </div>

  <hr class="my-4">

  <div class="row">
 <div class="col">
 <button class="w-100 btn btn-primary btn-lg" type="button" @click="signUp">회원가입</button>
 </div>
 <div class="col">
 <button class="w-100 btn btn-secondary btn-lg" type="button">취소</button>
 </div>
 </div>
</div>
</template>

<script>
import axios from "axios";

export default {
  name:'signup-view',
  data(){
    return{
        loginId:'',
        password:'',
        nickname:'',
    }
  },
  methods:{
    signUp(){
      axios.post('/api/members/add',{
        loginId:this.loginId,
        password:this.password,
        nickname:this.nickname
      }).then(()=>{
        this.$router.replace('/home');
      })
    }
  }

}

</script>

<style>
.container {
  max-width: 560px;
}
</style>