<template>
<div class="container">
 <el-result :title=data.title :sub-title=data.price>
    <template #icon>
      <el-image
          :src="`/api/display?fileName=${data.imgsrc}`"
          style="width:100%;max-width:500px;height:auto;display:block;"
      />
    </template>
    <template #extra>
    <div class="product-content mb-5">
      <p style="font-size:18px">상품 내용</p>
      <p style="font-size:15px; font-weight:200">  {{data.content}} </p>
    </div>
      <el-button type="warning" @click="EditItem">Modify</el-button>
      <el-button type="danger" @click="DeleteItem">Delete</el-button>
      <el-button type="info" @click="listItems">lists</el-button>
    </template>
  </el-result>
</div>
</template>

<script>
import axios from "axios";

export default {
  name:'Read-View',
  data(){
    return{
      data:{},
      NowPage:0,
      NowSize:0,


    }
  },
  mounted(){
    axios.get(`/api/products/${this.$route.params.productId}`).then((response)=>{
      console.log(response.data);
      this.data = response.data;
      this.data.price+='원';
      this.NowPage = this.$route.query.page;
      this.NowSize = this.$route.query.size;
      console.log(this.$route.query.page);
      console.log(this.$route.query.size);
    })
  },
  methods:{
    DeleteItem(){

      axios.get('/api/members/validate').then(()=>{
        axios.delete(`/api/products/${this.data.id}`).then(()=>{
          this.$router.replace(`/home?page=${this.NowPage}&size=${this.NowSize}`);
        })
      }).catch(()=>{
        alert('로그인을 해야 상품을 삭제할 수 있습니다.');
      })

    },
    EditItem(){
          this.$router.push(`/edit/${this.data.id}?page=${this.NowPage}&size=${this.NowSize}`);
    },
    listItems(){
      this.$router.push(`/home?page=${this.NowPage}&size=${this.NowSize}`);
    },
  }

}
</script>

<style>
.container{
  max-width:968px;
}
</style>